.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  background: rgba(0, 0, 0, 0.6);
}

.modal {
  display: block;
  width: 600px;
  max-width: 90%;
  height: 400px;
  max-height: 100%;
  position: fixed;
  z-index: 100;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: white;
  box-shadow: 0 0 60px 10px rgba(0, 0, 0, 0.9);
  overflow: auto;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 2px solid black;
  margin-left: 15px;
  margin-right: 15px;
}

.closeIcon {
  padding: 15px;
}

.modalTitle {
  font-weight: bold;
}

.modalContent {
  padding: 10px 20px 10px 20px;
}

.modalDescription {
  text-align: justify;
}

.subtitle {
  font-size: large;
}
